import './Services.scss';
import Title from "./Title";
import Placement_logo from '../img/png/placement.png';
import Destruction_logo from '../img/png/destruction.png';
import Transport_logo from '../img/png/transport.png';
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const Services = () => {
    const { language } = useContext(srcContext);

    return (
        <>
            <div className='Services_container'>
                <div id='Services_anchor'></div>
                <Title text={language.services.title} color="#210D0F"/>
                <div className='Services_content'>
                    <div className='first_half'>
                        <div className='content'>
                            <img alt='Placement logo' src={Placement_logo}/>
                            <h3>{language.services.sub_title1}</h3>
                            <h2>{language.services.content1}</h2>
                        </div>
                        <div className='content'>
                            <img alt='Destruction logo' src={Destruction_logo}/>
                            <h3>{language.services.sub_title2}</h3>
                            <h2>{language.services.content2}</h2>
                        </div>
                    </div>
                    <div className='second_half'>
                        <div className='content'>
                            <img alt='Transport logo' src={Transport_logo}/>
                            <h3>{language.services.sub_title3}</h3>
                            <h2>{language.services.content3}</h2>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
};

export default Services;

