const Title = (props) => {
    const titleStyle = {
        width: "100%", 
        height: "80px", 
        lineHeight: "80px",
        textAlign: "center", 
        color: props.color
    }
    return (
        <div className='Title_container' style={titleStyle}>
            <h4>{props.text}</h4>
        </div>
    )
};

export default Title;
