import './Navbar.scss';
import Phone from '../img/svg/phone.svg';
import Mail from '../img/svg/mail.svg';
import Clock from '../img/svg/clock.svg';
import Logo from '../img/svg/logo.svg';
import { useContext, useState } from "react";
import { srcContext } from "../SrcContext.js";

const Navbar = () => {
    const { value, setValue, language } = useContext(srcContext);
    const [engFlagStyle, setEngFlagStyle] = useState({opacity: "0.5"});
    const [norFlagStyle, setNorFlagStyle] = useState();

    function set(prop) {
        setValue(prop)
        if (prop === 'no') {
            setEngFlagStyle({opacity: "0.5"})
            setNorFlagStyle()
        }
        else {
            setNorFlagStyle({opacity: "0.5"})
            setEngFlagStyle()
        }
    }

    return (
        <nav>
            <div className='nav_contact_container'>
                <div><img src={Phone} alt='phone'/><a href='tel: +372 520 6028'><h1>+372 520 6028</h1></a></div>
                <div><img src={Mail} alt='mail'/><a href='mailto:mareknogu79@gmail.com'><h1>mareknogu79@gmail.com</h1></a></div>
                <div><img src={Clock} alt='clock'/><h1>M–F 9:00 – 17:00</h1></div>
                <div><img style={norFlagStyle} className='flag' src={require("../img/png/no_flag.png")} alt="flag" onClick={ () => set('no')}/><img style={engFlagStyle} className='flag' src={require("../img/png/en_flag.png")} alt="flag" onClick={ () => set('en')}/></div>
            </div>
            <div className='nav_hud_container'>
                <div className='left'>
                    <h2 id='HomeButton'>{language.nav.home}</h2>
                    <h2 id='AboutButton'>{language.nav.about}</h2>
                    <h2 id='WhyButton'>{language.nav.why}</h2>
                </div>
                <img id='logo' src={Logo} alt='logo'/>
                <div className='right'>
                    <h2 id='ServicesButton'>{language.nav.services}</h2>
                    <h2 id='GalleryButton'>{language.nav.gallery}</h2>
                    <h2 id='ContactButton'>{language.nav.contact}</h2>
                </div>
            </div>
        </nav>
    )
};

export default Navbar;


window.addEventListener('scroll', () => {
    const scrolled = window.scrollY;
    if (window.innerWidth > 1400) {
        if (Math.ceil(scrolled) < 200) {
            document.getElementById('logo').style.width = '';
        } else {
            document.getElementById('logo').style.width = '180px';
        }
    }
    
    addEvent('Home')
    addEvent('About')
    addEvent('Why')
    addEvent('Services')
    addEvent('Gallery')
    addEvent('Contact')
})

function addEvent(button) {
    document
        .getElementById(button + 'Button')
        .addEventListener('click', event => {
            document
            .getElementById(button + '_anchor')
            .scrollIntoView({behavior: 'smooth'})
        })
}

