import './Header.scss';
import HeaderImage from '../img/jpg/Header.jpg';
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const Header = () => {
    const { language } = useContext(srcContext);

    return (
        <div className='header'>
            <div id='Home_anchor'></div>
            <h5 id='top'>{language.header.top}</h5>
            <h5 id='bottom'>{language.header.bottom}</h5>
            <div className='background_container'>
                <img src={HeaderImage} alt='header BG'/>
            </div>
        </div>
    )
};

export default Header;