import './Contact.scss';
import LocationLogo from '../img/svg/location.svg';
import MailLogo from '../img/svg/mail.svg';
import PhoneLogo from '../img/svg/phone.svg';
import BGImage from '../img/jpg/Contact.JPG';
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const Contact = () => {
    const { language } = useContext(srcContext);

    return (
        <>
            <div className='Contact_container'>
                <div id='Contact_anchor'></div>
                <div className='Contact_content'>
                    <div className='content'>
                        <h2>{language.contact.title1}</h2>
                        <h2 id='name'>SHAG AS</h2>
                        <h2>{language.contact.content}</h2>
                    </div>
                    <div className='content'>
                        <h2>{language.contact.title2}</h2>
                        <div>
                            <img src={LocationLogo} alt='location'/>
                            <h2>Oslo</h2>
                        </div>
                        <div>
                            <img src={MailLogo} alt='meil' style={{filter: "invert(100%)", width: "21px"}}/>
                            <h2>mareknogu79@gmail.com</h2>
                        </div>
                        <div>
                            <img src={PhoneLogo} alt='phone nr.' style={{filter: "invert(100%)", width: "13px", margin: "0px 4px 0px 4px"}}/>
                            <h2>+372 520 6028</h2>
                        </div>
                    </div>
                </div>
                <div className='background_container'>
                    <img src={BGImage} alt='Background'/>
                </div>
            </div>
            <footer>
                <h1>{language.contact.footer}</h1>
            </footer>
        </>
    )
};

export default Contact;
