import './Why.scss';
import Title from "./Title";
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const Why = () => {
    const { language } = useContext(srcContext);

    return (
        <>
            <div className='why_container'>
                <div id='Why_anchor'></div>
                <Title text={language.why.title} color="white"/>
                <div className='why_content'>
                    <div className='first_half'>
                        <div className='content'>
                            <h3>{language.why.sub_title1}</h3>
                            <h2>{language.why.content1}</h2>
                        </div>
                        <div className='content'>
                            <h3>{language.why.sub_title2}</h3>
                            <h2>{language.why.content2}</h2>
                        </div>
                    </div>
                    <div className='second_half'>
                        <div className='content'>
                            <h3>{language.why.sub_title3}</h3>
                            <h2>{language.why.content3}</h2>
                        </div>
                        <div className='content'>
                            <h3>{language.why.sub_title4}</h3>
                            <h2>{language.why.content4}</h2>
                        </div>
                    </div>
                </div>
            </div>
            <div className='why_background'></div>
        </>
        
    )
};

export default Why;

