import './App.css';
import Layout from './components/Layout.js';
import Navbar from './components/Navbar.js';
import Header from './components/Header.js';
import About from './components/About.js';
import Why from './components/Why.js';
import Services from './components/Services.js';
import Gallery from './components/Gallery.js';
import Contact from './components/Contact.js';
import translation from './intl/et.json';
import { srcContext } from './SrcContext';
import { useEffect, useState } from 'react';

function App() {
  const [value, setValue] = useState('no');
  const [language, setLanguage] = useState(translation['no']);
  useEffect( () => { setLanguage(translation[value]); }, [value] );

  return (
    <srcContext.Provider value={{ value, setValue, language }}>
      <Layout>
        <Navbar/>
        <Header/>
        <About/>
        <Why/>
        <Services/>
        <Gallery/>
        <Contact/>
      </Layout>
    </srcContext.Provider>
  );
}

export default App;
