import './About.scss';
import AboutImage from '../img/jpg/About.jpg';
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const About = () => {
    const { language } = useContext(srcContext);

    return (
        <div className='about_container'>
            <div id='About_anchor'></div>
            <div className='text_container'>
                <h4 id='top'>{language.about.title}</h4>
                <h3 id='bottom'>{language.about.sub_title}</h3>
                <h2>{language.about.content}</h2>
            </div>
            <div className='image_container'>
                <img src={AboutImage} alt='About'/>
            </div>
        </div>
    )
};

export default About;
