import './Gallery.scss';
import Title from "./Title";
import Gallery_1 from '../img/jpg/Gallery_1.JPG';
import Gallery_2 from '../img/jpg/Gallery_2.JPG';
import Gallery_3 from '../img/jpg/Gallery_3.JPG';
import { useContext } from "react";
import { srcContext } from "../SrcContext.js";

const Gallery = () => {
    const { language } = useContext(srcContext);

    return (
        <>
            <div className='Gallery_container'>
                <div id='Gallery_anchor'></div>
                <Title text={language.gallery.title} color="white"/>
                <div className='Gallery_content'>
                    <div className='content'>
                        <img alt='Gallery' src={Gallery_1}/>
                    </div>
                    <div className='content'>
                        <img alt='Gallery' src={Gallery_2}/>
                    </div>
                    <div className='content'>
                        <img alt='Gallery' src={Gallery_3}/>
                    </div>
                </div>
            </div>
            <div className='Gallery_background'></div>
        </>
    )
};

export default Gallery;
